import React from 'react';
import { useEmptyStates } from './useEmptyStates';

import { CollectionOptimisticActions, ToolbarTitle } from '@wix/patterns';

import { Box, Text, TextButton } from '@wix/design-system';
import { BaseCollectionComponentConfig } from '../types';
import { useActionCell } from './useActionCell';
import { useBulkActionToolbar } from './useBulkActionToolbar';

export type UseCommonCollectionFeaturesParams = Pick<
  BaseCollectionComponentConfig,
  | 'actionCell'
  | 'toolbarTitle'
  | 'search'
  | 'emptyState'
  | 'collection'
  | 'bulkActionToolbar'
  | 'entityPageId'
> & {
  optimisticActions: CollectionOptimisticActions<any, any>;
};

export const useCommonCollectionFeatures = ({
  actionCell: actionCellConfig,
  bulkActionToolbar: bulkActionToolbarConfig,
  toolbarTitle,
  search,
  emptyState: emptyStateConfig,
  collection,
}: UseCommonCollectionFeaturesParams) => {
  const { emptyState } = useEmptyStates(emptyStateConfig);
  const actionCell = useActionCell({
    config: actionCellConfig,
    collectionId: collection.collectionId,
  });

  const bulkActionToolbar = useBulkActionToolbar({
    config: bulkActionToolbarConfig,
    collectionId: collection.collectionId,
  });

  const title = toolbarTitle && (
    <ToolbarTitle
      title={toolbarTitle.title}
      subtitle={{
        text: toolbarTitle.subtitle?.text ?? '',
        learnMore: toolbarTitle.subtitle?.learnMore,
        info: toolbarTitle.subtitle?.info && {
          content: (
            <Box direction="vertical" gap="6px">
              <Text
                dataHook="toolbar-title-subtitle-info-content"
                light
                size="small"
              >
                {toolbarTitle.subtitle?.info?.content.text}
              </Text>
              {toolbarTitle.subtitle?.info?.content.link && (
                <TextButton
                  dataHook="toolbar-title-subtitle-info-link"
                  size="small"
                  underline="always"
                  skin="light"
                  as="a"
                  rel="noopener"
                  target="_blank"
                  href={toolbarTitle.subtitle?.info?.content.link.url}
                >
                  {toolbarTitle.subtitle?.info?.content.link.label}
                </TextButton>
              )}
            </Box>
          ),
        },
      }}
      itemsLimit={toolbarTitle.itemsLimit}
      showTotal={toolbarTitle.showTotal}
    />
  );

  return {
    search: search?.shown !== false,
    title,
    emptyState,
    actionCell,
    bulkActionToolbar,
  };
};
